.chkbox {
  position: relative;
  padding-left: 35px;
  cursor: pointer;
  font-size: 17px;
  color: #696969;
  line-height: 17px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  padding-bottom: 30px;
}

/* Hide the browser's default checkbox */
.chkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom checkbox */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 18px;
  width: 18px;
  background-color: #fff;
  box-shadow: 0px 0px 2px 1px #dadada;
}

/* When the checkbox is checked, add a blue background */
.chkbox input:checked ~ .checkmark {
  background-color: #272974;
  border: 3px solid #fff;
}

.checkmark:after {
  position: absolute;
  display: none;
}

.chkbox input:checked ~ .checkmark:after {
  display: block;
}

.chkbox .checkmark:after {
  left: 9px;
  top: 5px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.chkbox select {
  border: 0px none;
  border-bottom: 1px solid #acacac;
  color: #696969;
  position: relative;
  top: -4px;
  width: 100%;
}

