  border-radius: 100px;
  z-index: 10;
  background: #272974;
  cursor: move;
  /* fallback if grab cursor is unsupported */
  cursor: grab;
  cursor: -moz-grab;
  cursor: -webkit-grab;
  box-shadow: inset -2px -2px 6px 2px rgba(0, 0, 0, 0.1);
  transition: width 0.1s;
}

.ui-slider-handle:focus {
  outline: none;
}

.ui-slider-handle:active {
  cursor: grabbing;
  cursor: -moz-grabbing;
  cursor: -webkit-grabbing;
}

.ui-state-active {
  width: 22px;
}

.range-wrapper {
  position: absolute;
  top: -50px;
  left: 50%;
  transform: translateX(-50%);
}

.range {
  background: #fff;
  white-space: nowrap;
  border-radius: 2px;
  font-size: 13px;
  letter-spacing: 0.02em;
  color: #555;
  width: 150px;
  z-index: 10;
  position: relative;
}

.range-value {
  width: 74px;
  padding: 8px 0;
  text-align: center;
  display: inline-block;
  transition: background 0.1s;
  color: #272974;
  font-size: 16px;
  padding-left: 5px;
  width: 100%;
}

.range-value sup {
  top: 0;
  font-size: 16px;
}

.range-divider {
  width: 1px;
  display: inline-block;
}

.range-divider:after {
  width: 1px;
  height: 100%;
  content: "-";
  color: #272974;
}

.range-alert {
  width: 16px;
  height: 16px;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  background: #fff;
  border-top-right-radius: 2px;
  border-bottom-right-radius: 2px;
  border: 1px solid #d2d2d2;
  color: #0E0E0E;
  line-height: 15px;
  transition: right 0.2s;
}

.range-alert.active {
  right: -15px;
}

.marker {
  position: absolute;
  top: 100%;
  padding-top: 16px;
  font-size: 17px;
  color: #a6a6a6;
  letter-spacing: 0.05em;
}

.marker:after {
  content: "";
  width: 1px;
  height: 8px;
  background: #d2d2d2;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.ui-widget.ui-widget-content {
  border: 1px solid #9b9b9b;
  background: #9b9b9b;
  height: 2px;
}

.ui-slider-handle {
  background: #272974 !important;
  border: 0px none !important;
  border-radius: 100% !important;
  top: -10px !important;
}

.breadcrumb-outer {
  padding-bottom: 30px;
  margin-top: 20px;
}

.newbread {
  background: #272974;
}

.breadcrumb {
  background: transparent;
}

