  letter-spacing: 0.05em;
}

.left-val {
  float: left;
}

.right-val {
  float: right;
}

.price-picker {
  padding: 8px 0;
  text-align: center;
  display: inline-block;
  transition: background 0.1s;
  color: #272974;
  font-size: 16px;
  padding-left: 5px;
  width: 100%;
}

.marker-0:after {
  display: none;
}

.marker-25 {
  left: 25%;
}

.marker-50 {
  left: 50%;
}

.marker-75 {
  left: 75%;
}

.marker-100 {
  right: 0;
}

.marker-100:after {
  display: none;
}

.gear-wrapper {
  position: absolute;
  top: -14px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 5;
  backface-visibility: hidden;
  width: 150px;
  height: 20px;
  overflow: hidden;
}

.gear-large {
  width: 40px;
  height: 40px;
  background: #d2d2d2;
  border-radius: 100px;
  position: absolute;
  top: 18px;
  left: 18px;
  transform-origin: 50% 50%;
  transition: top 0.15s;
}

.gear-large:after {
  position: absolute;
  content: "";
  width: 28px;
  height: 28px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #e8e8e8;
  border-radius: 100px;
}

.gear-large:nth-child(2) {
  background: #d2d2d2;
  left: auto;
  right: 18px;
}

.gear-large:nth-child(2) .gear-tooth {
  background: #d2d2d2;
}

.gear-large.active {
  top: 4px;
}

.gear-tooth {
  width: 22%;
  height: 114%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transform-origin: 0 0;
  background: #d2d2d2;
  border-radius: 2px;
}

.gear-tooth:nth-child(2) {
  transform: rotate(90deg) translate(-50%, -50%);
}

.gear-tooth:nth-child(3) {
  transform: rotate(45deg) translate(-50%, -50%);
}

.gear-tooth:nth-child(4) {
  transform: rotate(-45deg) translate(-50%, -50%);
}

